<template>
	<div class="box">
		<layout-box>
			<box-item title="品牌明细" showSubTitle subTitle="全部" @hand-sub-title="handlePage" :loading="loading">
				<div class="table-container">
					<el-table :data="brandList" stripe size="small" height="100%" width="100%" ref="brandTable">
						<el-table-column prop="name" label="品牌">
							<template slot-scope="scope">
								<div class="brand-name">
									<div class="brand-text">
										{{ scope.row.name }}
									</div>
									<div v-if="scope.row.shareFlag == 1" class="brand-tag">公用品牌</div>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="businessCategoryNickname" width="90px" label="行业"></el-table-column>
						<el-table-column prop="companyName" label="申报单位" show-overflow-tooltip></el-table-column>
					</el-table>
				</div>
			</box-item>
		</layout-box>
	</div>
</template>
<script>
import LayoutBox from '@/components/common/LayoutBox.vue';
import {_initBScroll, areaLevelCorAreaId} from '@/common/fn';
import BoxItem from '@/components/common/BoxItem.vue';
export default {
	props: {
		farmAreaInfo: {
			type: Object,
			default: () => areaLevelCorAreaId()
		}
	},
	data() {
		return {
			scroll: null,
			brandList: [],
			loading: false
		};
	},
	watch: {
		brandList: function(newData) {
			if (this.scroll) {
				this.$nextTick(() => {
					this.scroll.refresh();
				});
			}
		}
	},
	created() {},
	computed: {},
	mounted() {
		this.getBrandList();
		setTimeout(() => {
			this.scroll = _initBScroll(this.$refs.brandTable.$el.querySelector('.el-table__body-wrapper'));
		}, 200);
	},
	methods: {
		handlePage() {
			this.$router.push({
				name: 'farmProductBrandList'
			});
		},
		// 获取品牌列表
		getBrandList() {
			this.loading = true;
			this.$ajax
				.post(this.$api.brand.getPpBrandList, {
					farmAreaId: [this.farmAreaInfo.farmAreaId],
					farmAreaType: this.farmAreaInfo.farmAreaType,
					pageNo: 1,
					pageSize: 45
				})
				.then(res => {
					if (res.data.respCode === 0) {
						this.brandList = res.data.obj.list;
					}
					this.loading = false;
				});
		}
	},
	components: {
		LayoutBox,
		BoxItem
	}
};
</script>
<style scoped lang="scss">
// 表格皮肤样式
@import '~styles/tableSkin_default';
@import '~styles/scrollbarSkin_default';

.box {
	box-sizing: border-box;
	padding: 10px;
	height: 50%;
}
.table-container {
	position: relative;
	width: 100%;
	height: 100%;
	box-sizing: border-box;
	padding: 0 25px;
}
.brand-name {
	display: flex;
	align-items: center;
	.brand-text {
		font-size: 14px;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	.brand-tag {
		display: inline-block;
		border: 1px solid #feeb48;
		font-size: 12px;
		color: #feeb48;
		padding: 0 2px;
		border-radius: 2px;
		white-space: nowrap;
		margin-left: 6px;
	}
}

/deep/ .el-table--scrollable-y .el-table__body-wrapper {
	overflow: hidden;
}

/deep/ .el-table--scrollable-x .el-table__body-wrapper {
	overflow: hidden;
}
</style>



